import { StrictMode } from 'react';
import { createRoot } from 'react-dom/client';
import { InitializationConfig } from '../types';
import { LaunchDarklyToolbar } from './ui/Toolbar/LaunchDarklyToolbar';

const TOOLBAR_DOM_ID = 'ld-toolbar';

export default function mount(rootNode: HTMLElement, config: InitializationConfig) {
  const cleanup: (() => void)[] = [];

  // Make sure host applications don't mount the toolbar multiple
  if (document.getElementById(TOOLBAR_DOM_ID) != null) {
    return () => {
      cleanup.forEach((fn) => fn());
    };
  }

  const { host, reactMount } = buildDom();

  const reactRoot = createRoot(reactMount);
  reactRoot.render(
    <StrictMode>
      <LaunchDarklyToolbar
        domId={TOOLBAR_DOM_ID}
        baseUrl={config.baseUrl}
        devServerUrl={config.devServerUrl}
        projectKey={config.projectKey}
        flagOverridePlugin={config.flagOverridePlugin}
        eventInterceptionPlugin={config.eventInterceptionPlugin}
        pollIntervalInMs={config.pollIntervalInMs}
        position={config.position}
      />
    </StrictMode>,
  );
  cleanup.push(() =>
    // `setTimeout` helps to avoid "Attempted to synchronously unmount a root while React was already rendering."
    setTimeout(() => reactRoot.unmount(), 0),
  );

  rootNode.appendChild(host);
  cleanup.push(() => host.remove());

  return () => {
    cleanup.forEach((fn) => fn());
  };
}

function buildDom() {
  const host = document.createElement('div');
  host.id = TOOLBAR_DOM_ID;
  host.style.inset = '0';
  host.style.width = '0px';
  host.style.height = '0px';
  host.style.position = 'absolute';
  host.style.zIndex = '2147400100';

  const shadowRoot = host.attachShadow({ mode: 'open' });

  const style = document.createElement('style');
  const reactMount = document.createElement('div');

  // Multi-layered robust approach to find toolbar styles:
  
  // 1. First priority: Look for styles with our custom data attribute
  let toolbarStyleElements = document.head.querySelectorAll('style[data-toolbar="launchdarkly"]');
  
  // 2. Second priority: Look for styles containing LaunchPad UI design tokens
  if (toolbarStyleElements.length === 0) {
    toolbarStyleElements = document.head.querySelectorAll('style');
    toolbarStyleElements = Array.from(toolbarStyleElements).filter((styleEl) => 
      styleEl.textContent?.includes('--lp-')
    );
  }
  
  // 3. Third priority: Look for Vanilla Extract generated class names (toolbar-specific patterns)
  if (toolbarStyleElements.length === 0) {
    toolbarStyleElements = document.head.querySelectorAll('style');
    toolbarStyleElements = Array.from(toolbarStyleElements).filter((styleEl) => {
      const content = styleEl.textContent || '';
      return (
        content.includes('bno7jr') ||  // Tabs component classes
        content.includes('iza2ve') ||  // List component classes  
        content.includes('f9csi') ||   // Toolbar component classes
        content.includes('yyeomx') ||  // GenericHelpText classes
        content.includes('injzd') ||   // OverrideIndicator classes
        content.includes('skhe0p')     // EventsList classes
      );
    });
  }
  
  // 4. Last resort: Generic underscore pattern (Vanilla Extract default)
  if (toolbarStyleElements.length === 0) {
    toolbarStyleElements = document.head.querySelectorAll('style');
    toolbarStyleElements = Array.from(toolbarStyleElements).filter((styleEl) => 
      styleEl.textContent?.includes('_')
    );
  }

  const toolbarStyles = Array.from(toolbarStyleElements)
    .map((styleEl) => styleEl.textContent || '')
    .join('\n');

  if (toolbarStyles) {
    style.textContent = toolbarStyles;
    shadowRoot.appendChild(style);
  }

  reactMount.dataset.name = 'react-mount';
  reactMount.id = 'ld-toolbar-react-mount';
  shadowRoot.appendChild(reactMount);

  return { host, reactMount };
}
